<template>
  <div>
    <!--面包屑导航-->
    <div class="breadcrumb">
      <div class="container">
        <h2>
          <a href>首页</a>&gt;
          <a href>公司动态</a>
        </h2>
      </div>
    </div>
    <!--页面主体-->
    <div class="main container">
      <div class="news">
        <ul>
          <li v-for="(item, index) in data" :key="index">
            <span>{{ item.pubTime | date }}</span>
            <router-link :to="`/news_detail/${item.nid}`">
              {{item.title}}
            </router-link>
          </li>
        </ul>
      </div>
      <!-- 分页导航-->
      <div class="pages">
        <a v-if="pageNum > 1" @click="getData(pageNum - 1)">上一页</a>
        <a v-else class="default">上一页</a>
        <a
          v-for="(item, index) in pageCount"
          :key="index"
          :class="{ cur: item == pageNum }"
          @click="getData(item)"
        >{{ item }}</a>
        <a v-if="pageNum < pageCount" @click="getData(pageNum + 1)">下一页</a>
        <a v-else class="default">下一页</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: [],
      pageCount: 0,
      pageNum: 0,
      pageSize: 0,
      totalRecord: 0,
    };
  },
  methods: {
    getData(pno) {
      this.axios
        .get("news_select.php?pageNum=" + pno)
        .then((res) => {
          // console.log(res);
          this.data = res.data.data;
          this.pageCount = res.data.pageCount;
          this.pageNum = res.data.pageNum;
          this.pageSize = res.data.pageSize;
          this.totalRecord = res.data.totalRecord;
        })
        .catch((err) => {
          console.error(err);
        });
    },
  },
  mounted() {
    this.getData(1);
  },
};
</script>

<style>
.pages {
  -moz-user-select: none; /*火狐*/
  -webkit-user-select: none; /*webkit浏览器*/
  -ms-user-select: none; /*IE10*/
  -khtml-user-select: none; /*早期浏览器*/
  user-select: none;
}

.pages > a {
  cursor: pointer;
}
</style>
